@property --gradient-angle {
  syntax: "<angle>";
  inherits: false;
  initial-value: 0deg;
}

@keyframes rotation {
  0% {
    --gradient-angle: 0deg;
  }

  100% {
    --gradient-angle: 360deg;
  }
}

:root {
  --clr-1: #052b2f;
  --clr-2: #073438;
  --clr-3: #f60;
  --clr-4: #fe0;
  --clr-5: #eff;
}

.body {
  background: var(--clr-1);
}

.container {
  justify-content: center;
  gap: 20px;
  margin: 100px auto;
  display: flex;
}

.key {
  aspect-ratio: 1 / 1.5;
  background: var(--clr-2);
  text-align: center;
  color: #f60;
  width: 50px;
  height: 150px;
  width: 100px;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: .5rem;
  align-items: center;
  font-size: 1.5rem;
  line-height: 150px;
  transition: all 90ms;
  display: inline-block;
  position: relative;
  box-shadow: inset 1px 1px 8px #000;
}

.key:before, .key:after {
  content: "";
  z-index: -1;
  background: conic-gradient( from var(--gradient-angle), var(--clr-3), var(--clr-4), var(--clr-5), var(--clr-4), var(--clr-3) );
  border-radius: inherit;
  animation: rotation 15s linear infinite;
  position: absolute;
  top: -.1rem;
  bottom: -.1rem;
  left: -.1rem;
  right: -.1rem;
}

.key:after {
  filter: blur(3.5rem);
}

.playing {
  background-color: #ffc600;
  transform: scale(1.1);
  box-shadow: 2px 2px 10px rgba(0, 0, 0, .5);
}

